.header {
    margin-bottom: 15px;

    &__top {
        padding: 10px 0;
        background-color: $black; }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-top: 10px; }

    &__title {
        margin-left: 140px; }

    &__name {
        @include fontRB; }

    &__contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 12px;

        a {
            font-size: 20px;
            color: $black;
            transition: 0.3s color ease;

            &:hover {
                color: $gold; } } } }
