.bag {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__icon {
        width: 50px;
        height: 50px;

        svg {
            width: 100%;
            height: 100%;

            fill: $black;

            transition: 0.3s fill ease; }

        &:hover {
            svg {
                fill: $gold; } } }

    &__text {
        font-size: 14px; } }
