html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-bag {
  width: 1em;
  height: 1em;
  fill: #505070;
}

.icon-fb {
  width: 1em;
  height: 1em;
  fill: #1d1d1d;
}

.icon-insta {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-macaron {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-rectangle {
  width: 2.14em;
  height: 1em;
  fill: #fff;
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto-Medium";
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Roboto-Regular', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.4;
}

a {
  text-decoration: none;
}

.out {
  overflow: hidden;
}

.container {
  position: relative;
  width: 1200px;
  margin: 0 auto;
}

.uppercase {
  text-transform: uppercase;
}

.logo {
  display: block;
  position: absolute;
  top: -10px;
  left: 0;
  width: 120px;
  height: 120px;
  background-color: #ffffff;
  box-shadow: 0 2px 10px -5px #000;
  z-index: 2;
}

.logo--bottom {
  top: auto;
  bottom: 0;
}

.main__preview {
  margin-bottom: 50px;
}

.main__slider {
  margin-bottom: 50px;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.header {
  margin-bottom: 15px;
}

.header__top {
  padding: 10px 0;
  background-color: #1d1d1d;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.header__title {
  margin-left: 140px;
}

.header__name {
  font-family: 'Roboto-Bold', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.header__contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
}

.header__contact a {
  font-size: 20px;
  color: #1d1d1d;
  transition: 0.3s color ease;
}

.header__contact a:hover {
  color: #c2a978;
}

.main-nav {
  display: flex;
}

.main-nav__list {
  display: flex;
  margin: 0 auto;
}

.main-nav__item {
  display: flex;
  margin-right: 20px;
}

.main-nav__item:last-child {
  margin-right: 0;
}

.main-nav__link {
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  transition: 0.3s color ease;
}

.main-nav__link:hover {
  color: #c9c1c1;
}

.main-nav__link.is-active {
  color: #c2a978;
}

.social__list {
  display: flex;
}

.social__item {
  display: flex;
  margin-right: 20px;
}

.social__item:last-child {
  margin-right: 0;
}

.social__link {
  width: 40px;
  height: 40px;
}

.social__link svg {
  width: 100%;
  height: 100%;
  fill: #1d1d1d;
  transition: 0.3s fill ease;
}

.social__link:hover svg {
  fill: #c2a978;
}

.bag {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bag__icon {
  width: 50px;
  height: 50px;
}

.bag__icon svg {
  width: 100%;
  height: 100%;
  fill: #1d1d1d;
  transition: 0.3s fill ease;
}

.bag__icon:hover svg {
  fill: #c2a978;
}

.bag__text {
  font-size: 14px;
}

.footer {
  background-color: #1d1d1d;
  color: #ffffff;
  margin-top: 80px;
}

.footer__inner {
  display: flex;
  justify-content: center;
  padding: 10px 0;
}

.footer__contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
}

.footer__contact a {
  font-size: 20px;
  color: #ffffff;
  transition: 0.3s color ease;
}

.footer__contact a:hover {
  color: #c2a978;
}

.footer__social .social {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.footer__social .social__link:hover svg {
  fill: #c2a978;
}

.footer__social .social svg {
  fill: #ffffff;
}

.footer__copyright {
  padding-bottom: 10px;
  font-size: 12px;
  text-align: center;
  color: #ffffff;
}

.footer__copyright b {
  font-size: 14px;
  font-family: 'Roboto-Medium', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.hero {
  height: 300px;
  color: #ffffff;
  overflow: hidden;
  background-color: #1d1d1d;
  margin-bottom: 50px;
}

.hero__title {
  height: 300px;
  font-family: 'Roboto-Regular', sans-serif;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  display: flex;
  margin-left: auto;
  align-items: center;
}

.hero__inner {
  position: relative;
  display: flex;
}

.hero__rectangle svg {
  width: 1000px;
  height: 467px;
}

.hero__media {
  position: absolute;
  top: 50px;
  left: 150px;
}

.product {
  margin-bottom: 50px;
  padding: 20px;
  box-shadow: 0 2px 10px -5px #000;
  border-radius: 5px;
  background-color: rgba(29, 29, 29, 0.05);
}

.product__inner {
  display: flex;
  align-items: center;
}

.product__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 680px;
  margin: 0 auto;
}

.product__image {
  width: 300px;
  height: 200px;
  background-image: url("../img/macarons.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.product__title {
  position: relative;
  padding-top: 40px;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.product__title-top {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  width: 100%;
}

.product__title-top svg {
  width: 30px;
  height: 30px;
  fill: #1d1d1d;
}

.product__title-top:before, .product__title-top:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 310px;
  height: 1px;
  background-color: #1d1d1d;
}

.product__title-top:after {
  left: auto;
  right: 0;
}

.product__preview {
  width: 70%;
  margin-bottom: 20px;
  text-align: center;
}

.product__price {
  margin-bottom: 20px;
  font-family: 'Roboto-Bold', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  color: #c2a978;
}

.product__price span {
  font-size: 20px;
  font-family: 'Roboto-Medium', sans-serif;
  font-style: normal;
  font-weight: normal;
}

.product__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.product__text {
  padding: 0 30px;
  font-size: 12px;
  text-align: center;
}

.button {
  display: flex;
  justify-content: center;
  width: 180px;
  height: 40px;
  padding: 10px 0;
  font-family: 'Roboto-Regular', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #ffffff;
  border: 1px solid #1d1d1d;
  border-radius: 30px;
  background-color: #1d1d1d;
  transition: 0.4s all ease;
}

.button:hover {
  background-color: #ffffff;
  color: #1d1d1d;
}

.button--inverse {
  background-color: #ffffff;
  color: #1d1d1d;
}

.button--inverse:hover {
  background-color: #1d1d1d;
  color: #ffffff;
}

.button--transparent {
  background-color: transparent;
  color: #ffffff;
  border-color: #ffffff;
}

.button--transparent:hover {
  background-color: #ffffff;
  color: #1d1d1d;
  border-color: #1d1d1d;
}

.counter {
  display: flex;
  align-items: center;
}

.counter__title {
  margin-right: 20px;
}

.counter__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 40px;
  font-size: 18px;
  color: #ffffff;
  background-color: #1d1d1d;
  border: 1px solid #1d1d1d;
  border-radius: 30px  0 0 30px;
  transition: 0.4s all ease;
}

.counter__button--reverse {
  border-radius: 0 30px 30px 0;
}

.counter__button:hover {
  background-color: #ffffff;
  color: #1d1d1d;
}

.counter__inner {
  display: flex;
}

.counter__inner input {
  width: 120px;
  border: 1px solid #1d1d1d;
  border-left: none;
  border-right: none;
  text-align: center;
}

.slider {
  width: 100%;
  height: 100%;
}

.slider__slide {
  position: relative;
  width: 100%;
  height: 500px;
}

.slider .slick-arrow {
  position: absolute;
  bottom: 50%;
  left: 100px;
  width: 25px;
  height: 40px;
  transform: translateY(50%);
  font-size: 0;
  z-index: 5;
  background-color: transparent;
}

.slider .slick-arrow:before, .slider .slick-arrow:after {
  content: '';
  position: absolute;
  top: 16px;
  left: 10px;
  display: block;
  width: 3px;
  height: 20px;
  background-color: #ffffff;
  transform: rotate(45deg);
  transition: 0.4s background-color ease;
  border-radius: 1px;
}

.slider .slick-arrow:hover::before, .slider .slick-arrow:hover::after {
  background-color: #c2a978;
}

.slider .slick-next:before, .slider .slick-next:after {
  right: 10px;
  left: auto;
}

.slider .slick-arrow:after {
  transform: rotate(-45deg);
  top: 4px;
}

.slider .slick-prev:after {
  transform: rotate(-135deg);
}

.slider .slick-prev:before {
  transform: rotate(135deg);
}

.slider .slick-next {
  left: auto;
  right: 100px;
}

.slider .slick-dots {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.slider .slick-dots li {
  display: flex;
  margin-right: 20px;
}

.slider .slick-dots button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border: 1px solid #c2a978;
  background: #ffffff;
  border-radius: 50%;
}

.slider .slick-dots .slick-active {
  position: relative;
}

.slider .slick-dots .slick-active button:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(194, 169, 120, 0.6);
  border-radius: 50%;
}

.slider .slick-slide > div {
  display: flex;
}

.slide {
  position: relative;
  height: 100%;
  text-align: center;
}

.slide__content {
  position: absolute;
  top: 50px;
  left: 20%;
  z-index: 2;
}

.slide img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide__title {
  font-family: 'Roboto-Medium', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  color: #c2a978;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.slide__to-order {
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.slide__text {
  margin-bottom: 30px;
  color: #ffffff;
}

.slide__subtitle {
  font-size: 24px;
  color: #c2a978;
  text-transform: uppercase;
}

.slide__button {
  display: flex;
  justify-content: center;
}
